* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.stars {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  color: #060b35;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.card__image {
  align-self: center;
  width: 160px;
  margin-bottom: 40px;
  display: block;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__label {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
  display: block;
}

.card__review {
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__review-label {
  text-align: center;
  margin-left: 17px;
  font-size: 10px;
  line-height: 14px;
  display: block;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  margin-bottom: 16px;
  padding-block: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: block;
}

.card__buy:hover {
  color: #00acdc;
